"use client"

import React, { useEffect, useState, useRef } from 'react';
import { useRouter } from 'next/navigation';
import { useAppSelector } from '../../store/store';
import { selectIsLoggedIn } from '../../store/authSlice';
import styles from './page.module.css';
import Cookies from 'js-cookie';

interface UserResponseDTO {
    username: string;
    social: boolean;
    nickname: string;
    email: string;
}

interface UserRequestDTO {
    username: string;
    password?: string;
    nickname?: string;
    email?: string;
}

export default function ProfilePage() {
    const router = useRouter();
    const isLoggedIn = useAppSelector(selectIsLoggedIn);

    const [userData, setUserData] = useState<UserResponseDTO | null>(null);
    const [currentPassword, setCurrentPassword] = useState<string>('');
    const [newPassword, setNewPassword] = useState<string>('');
    const [newPasswordConfirm, setNewPasswordConfirm] = useState<string>('');
    const [nickname, setNickname] = useState<string>('');
    const [emailId, setEmailId] = useState<string>('');
    const [emailDomain, setEmailDomain] = useState<string>('naver.com');
    const [isSocialUser, setIsSocialUser] = useState<boolean>(false);
    const [loading, setLoading] = useState<boolean>(true);
    const [error, setError] = useState<string | null>(null);

    // 이메일 도메인 셀렉트 박스 Ref
    const emailDomainSelectRef = useRef<HTMLButtonElement>(null);
    const [isEmailDomainSelectOpen, setIsEmailDomainSelectOpen] = useState(false);

    const availableDomains = ['naver.com', 'gmail.com', 'daum.net', 'hanmail.net', 'kakao.com', 'outlook.com', '직접입력'];

    useEffect(() => {


        const fetchUserData = async () => {
            try {
                const xsrfToken = Cookies.get('XSRF-TOKEN');
                const response = await fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/user`, {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                        ...(xsrfToken && { 'X-XSRF-TOKEN': xsrfToken }),
                    },
                    credentials: 'include',
                });

                if (!response.ok) {
                    // 401 Unauthorized 에러 발생 시 로그인 페이지로 리디렉션
                    if (response.status === 401) {
                        alert('로그인이 필요합니다.');
                        router.push('/login');
                        return;
                    }
                    const errorData = await response.json();
                    throw new Error(errorData.message || '사용자 정보를 불러오는데 실패했습니다.');
                }

                const data: UserResponseDTO = await response.json();
                setUserData(data);
                setNickname(data.nickname);
                setIsSocialUser(data.social);
                if (data.email) {
                    const [id, domain] = data.email.split('@');
                    setEmailId(id);
                    setEmailDomain(domain);
                }
            } catch (err: any) {
                setError(err.message);
                alert(`오류: ${err.message}`);
            } finally {
                setLoading(false);
            }
        };

        fetchUserData();
    }, [isLoggedIn, router]);

    const handleEmailDomainChange = (domain: string) => {
        setEmailDomain(domain);
        setIsEmailDomainSelectOpen(false);
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!userData) return;

        const updatedData: UserRequestDTO = {
            username: userData.username,
            nickname: nickname,
        };

        if (!isSocialUser) {
            // 자체 로그인 사용자만 비밀번호와 이메일 수정 가능
            if (newPassword) {
                if (newPassword !== newPasswordConfirm) {
                    alert('새 비밀번호와 비밀번호 확인이 일치하지 않습니다.');
                    return;
                }
                // 현재 비밀번호 확인 로직은 백엔드에서 처리한다고 가정
                updatedData.password = newPassword;
            }
            updatedData.email = `${emailId}@${emailDomain}`;
        }

        try {
            const xsrfToken = Cookies.get('XSRF-TOKEN');
            const response = await fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/user`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                    ...(xsrfToken && { 'X-XSRF-TOKEN': xsrfToken }),
                },
                credentials: 'include',
                body: JSON.stringify(updatedData),
            });

            if (response.ok) {
                alert('회원 정보가 성공적으로 수정되었습니다.');
                // 필요한 경우 사용자 정보 새로고침 또는 Redux 상태 업데이트
                // router.reload(); // 페이지 새로고침
            } else if (response.status === 403) {
                const errorData = await response.json();
                alert(`권한 오류: ${errorData.error || '수정 권한이 없습니다.'}`);
            } else {
                const errorData = await response.json();
                throw new Error(errorData.message || '회원 정보 수정에 실패했습니다.');
            }
        } catch (err: any) {
            setError(err.message);
            alert(`오류: ${err.message}`);
        }
    };

    if (loading) return <div className={styles.loading}>Loading...</div>;
    if (error) return <div className={styles.error}>Error: {error}</div>;
    if (!userData) return <div className={styles.noData}>사용자 정보를 찾을 수 없습니다.</div>;

    return (
        <div className={styles.pageWrapper}> {/* 최상위 div에 pageWrapper 적용 */}
            <div className={styles.profileCard}> {/* 기존 container에 profileCard 적용 */}
                <div className={styles.headerSection}>
                    <h2 className={styles.pageTitle}>개인 정보 수정</h2>
                </div>
                <div className={styles.formSection}>
                    <form onSubmit={handleSubmit}>
                        <div className={styles.inputGroup}>
                            <label htmlFor="username" className={styles.label}>아이디</label>
                            <div className={styles.inputFieldContainer}>
                                <input
                                    data-testid="input-box"
                                    id="username"
                                    name="username"
                                    type="text"
                                    readOnly
                                    className={styles.readOnlyInput}
                                    value={userData.username}
                                />
                            </div>
                        </div>

                        {!isSocialUser && (
                            <>
                                <div className={styles.inputGroup}>
                                    <label htmlFor="currentPassword" className={styles.label}>현재 비밀번호</label>
                                    <div className={styles.inputFieldContainer}>
                                        <input
                                            data-testid="input-box"
                                            id="currentPassword"
                                            name="currentPassword"
                                            placeholder="현재 비밀번호를 입력해 주세요"
                                            type="password"
                                            autoComplete="off"
                                            className={styles.input}
                                            value={currentPassword}
                                            onChange={(e) => setCurrentPassword(e.target.value)}
                                        />
                                    </div>
                                </div>

                                <div className={styles.inputGroup}>
                                    <label htmlFor="newPassword" className={styles.label}>새 비밀번호</label>
                                    <div className={styles.inputFieldContainer}>
                                        <input
                                            data-testid="input-box"
                                            id="newPassword"
                                            name="newPassword"
                                            placeholder="새 비밀번호를 입력해 주세요"
                                            type="password"
                                            autoComplete="off"
                                            className={styles.input}
                                            value={newPassword}
                                            onChange={(e) => setNewPassword(e.target.value)}
                                        />
                                    </div>
                                </div>

                                <div className={styles.inputGroup}>
                                    <label htmlFor="newPasswordConfirm" className={styles.label}>새 비밀번호 확인</label>
                                    <div className={styles.inputFieldContainer}>
                                        <input
                                            data-testid="input-box"
                                            id="newPasswordConfirm"
                                            name="newPasswordConfirm"
                                            placeholder="새 비밀번호를 다시 입력해 주세요"
                                            type="password"
                                            autoComplete="off"
                                            className={styles.input}
                                            value={newPasswordConfirm}
                                            onChange={(e) => setNewPasswordConfirm(e.target.value)}
                                        />
                                    </div>
                                </div>
                            </>
                        )}

                        <div className={styles.inputGroup}>
                            <label htmlFor="nickname" className={styles.label}>닉네임</label>
                            <div className={styles.inputFieldContainer}>
                                <input
                                    data-testid="input-box"
                                    id="nickname"
                                    name="nickname"
                                    placeholder="닉네임을 입력해 주세요"
                                    type="text"
                                    className={styles.input}
                                    value={nickname}
                                    onChange={(e) => setNickname(e.target.value)}
                                />
                            </div>
                        </div>

                        {!isSocialUser && (
                            <div className={styles.inputGroup}>
                                <label htmlFor="emailId" className={styles.label}>이메일</label>
                                <div className={styles.emailInputContainer}>
                                    <input
                                        id="emailId"
                                        name="emailId"
                                        placeholder="이메일 주소 입력"
                                        type="text"
                                        className={styles.emailIdInput}
                                        value={emailId}
                                        onChange={(e) => setEmailId(e.target.value)}
                                    />
                                    <div className={styles.emailSeparator}>@</div>
                                    <div className={styles.emailDomainSelectWrapper}>
                                        <button
                                            type="button"
                                            className={styles.emailDomainButton}
                                            onClick={() => setIsEmailDomainSelectOpen(prev => !prev)}
                                            ref={emailDomainSelectRef}
                                        >
                                            {emailDomain}
                                        </button>
                                        {isEmailDomainSelectOpen && (
                                            <div className={styles.emailDomainOptions}>
                                                {availableDomains.map(domain => (
                                                    <button
                                                        key={domain}
                                                        type="button"
                                                        onClick={() => handleEmailDomainChange(domain)}
                                                        className={styles.emailDomainOptionItem}
                                                    >
                                                        {domain}
                                                    </button>
                                                ))}
                                                {emailDomain === '직접입력' && (
                                                    <input
                                                        type="text"
                                                        className={styles.emailDirectInput}
                                                        placeholder="직접입력"
                                                        value={emailDomain === '직접입력' ? '' : emailDomain}
                                                        onChange={(e) => setEmailDomain(e.target.value)}
                                                        onClick={(e) => e.stopPropagation()} // 이벤트 버블링 방지
                                                    />
                                                )}
                                            </div>
                                        )}
                                    </div>
                                </div>
                            </div>
                        )}

                        <div className={styles.buttonGroup}>
                            <button type="button" onClick={() => router.push('/mypage/withdraw')} className={`${styles.bottomButton} ${styles.withdrawButton}`}>탈퇴하기</button>
                            
                        </div>
                        <div className={styles.buttonGroup}>
                            <button type="submit" className={`${styles.bottomButton} ${styles.saveButton}`}>수정 내용 저장하기</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
}
